<!-- 【微信注册信息】管理页面 -->
<template>
  <div class="app-container">
    <!-- 表单弹窗 -->
    <userRegister-form ref="userRegisterDialog" @reloadList="getList"/>
    <!-- 查询条件 -->
    <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
      <el-form-item label="姓名" prop="realName">
        <el-input v-model="queryForm.realName" placeholder="请输入姓名" size="small"/>
      </el-form-item>
<!--      <el-form-item label="角色" prop="roleType">-->
<!--        <el-select v-model="queryForm.roleType" placeholder="请选择角色" size="small" clearable>-->
<!--          <el-option v-for="dict in roleTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>-->
<!--        </el-select>-->
<!--      </el-form-item>-->
      <el-form-item>
        <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
        <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
      </el-form-item>
    </el-form>
    <!-- 查询按钮 -->
    <el-row :gutter="10" class="mb8">
      <el-col :span="1.5">
        <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增微信注册信息'})" v-hasPermi="['userRegister:add']">新增</el-button>
      </el-col>
      <el-col :span="1.5">
        <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['userRegister:delete']">批量删除</el-button>
      </el-col>
      <right-toolbar @queryTable="getList"></right-toolbar>
    </el-row>
    <!-- 表格区域 -->
    <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
      <el-table-column type="selection" align="center" width="50"/>
      <el-table-column label="id"  prop="id" v-if="false"/>
      <el-table-column label="序号" type="index" width="50"/>
      <el-table-column label="头像" prop="headPicture" sortable>
        <template slot-scope="scope">
          <el-image :src="formatUrl(scope.row.headPicture)" class="column-image"></el-image>
        </template>
      </el-table-column>
      <el-table-column label="角色" prop="roleType" sortable>
        <template slot-scope="scope">
          <span v-for="dict in roleTypeDict" v-if="dict.dictValue==scope.row.roleType">{{dict.dictLabel}}</span>
        </template>
      </el-table-column>
      <el-table-column label="微信昵称" prop="wxUserInfoId" sortable>
        <template slot-scope="scope">
          <span v-for="wxUserInfo in wxUserInfoDict" v-if="wxUserInfo.id==scope.row.wxUserInfoId">{{wxUserInfo.nickName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="登录名" prop="loginName" sortable/>
      <el-table-column label="姓名" prop="realName" sortable/>
<!--      <el-table-column label="密码" prop="password" sortable/>-->
      <el-table-column label="邮箱" prop="email" sortable/>
      <el-table-column label="手机号" prop="phone" sortable/>
      <el-table-column label="身份证号" prop="idCard" sortable/>
      <el-table-column label="详细地址" prop="address" show-overflow-tooltip sortable/>
      <el-table-column label="操作" align="center"  width="300" fixed="right">
        <template slot-scope="scope">
          <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看微信注册信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['userRegister:view']">查看</el-button>
          <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改微信注册信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['userRegister:edit']">修改</el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['userRegister:delete']">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="pagination">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :background="true"
        :current-page="queryForm.current"
        :page-sizes="[5,10,20,30,50]"
        :page-size="queryForm.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import userRegisterForm from './userRegisterForm'
  export default {
    components: {userRegisterForm},
    data() {
      return {
        loading: true,//是否加载完成
        selectedData: [],//勾选数据
        total: 0, //记录数
        wxUserInfoDict:[],
        dataList: [],
        queryForm: {
          current: 1,
          size: 10,
          headPicture: "",
          roleType: "",
          loginName: "",
          realName: "",
          password: "",
          email: "",
          phone: "",
          idCard: "",
          address: "",
          wxUserInfoId: ""
        }
      };
    },
    mounted() {
      //初始化数据
      this.getList();
      //初始化【基础表id】
      this.getWxUserInfoDict();
    },
    computed: {
      //【角色】
      roleTypeDict() {
        return this.$store.getters.getDictArray('role_type');
      },
    },
    methods: {
      //点击搜索
      handleQuery() {
        this.queryForm.current = 1;
        this.getList();
      },
      //查询列表数据
      getList() {
        this.$request.doPost('/api/wxUserRegister/getPage', this.queryForm).then(res => {
          if (res.isOk) {
            this.loading = false;
            this.dataList = res.obj.records;
            this.total = res.obj.total;
          }
        })
      },
      //【基础表id】
      getWxUserInfoDict(){
        this.$request.doPost('/api/wxUserInfo/getList', {}).then(res => {
          if (res.isOk) {
            this.wxUserInfoDict=res.list;
          }
        })
      },
      //重置查询
      resetQuery() {
        this.$refs['queryForm'].resetFields();
      },
      //打开弹窗
      openDialog(onLoad) {
        this.$refs.userRegisterDialog.open(onLoad);
      },
      //选择页数
      handleSizeChange: function (size) {
        this.queryForm.size = size;
        this.getList();
      },
      //选择页码
      handleCurrentChange: function (currentPage) {
        this.queryForm.current = currentPage;
        this.getList();
      },
      //获取勾选数据
      handleSelectionChange(data) {
        this.selectedData = data;
      },
      //勾选批量删除
      handleDeleteAll() {
        const that = this;
        that.showConfirm('确定删除选中的除数据吗?', function () {
          if (that.selectedData.length > 0) {
            that.selectedData.forEach(function (item, index) {
              that.delete(item.id);
            });
            that.showSuccess("删除成功")
          } else {
            that.showWarn("至少选择一条数据！")
          }
        });
      },
      //选中行删除
      handleDelete(id) {
        const that = this;
        that.showConfirm('确定删除选中的除数据吗?', function () {
          that.delete(id);
          that.showSuccess("删除成功");
        });
      },
      //删除方法
      delete(id) {
        this.$request.doGet('/api/wxUserRegister/delete?id=' + id).then(res => {
          if (res.isOk) {
            this.getList();
          };
        });
      },
    }
  };
</script>



