<!-- 【通讯信息】管理页面 -->
<template>
    <div class="app-container">
        <!-- 表单弹窗 -->
        <userMsg-form ref="userMsgDialog" @reloadList="getList"/>
        <!-- 查询条件 -->
        <el-form :model="queryForm" ref="queryForm" :inline="true" label-width="68px">
            <el-form-item label="所属用户" prop="userId">
                <el-select v-model="queryForm.userId" placeholder="请选择所属用户" size="small">
                    <el-option v-for="wxUserRegister in wxUserRegisterDict" :key="wxUserRegister.id" :value="wxUserRegister.id" :label="wxUserRegister.loginName"/>
                </el-select>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
                <el-select v-model="queryForm.sex" placeholder="请选择性别" size="small" clearable>
                    <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="标题" prop="title">
                <el-input v-model="queryForm.title" placeholder="请输入标题" size="small"/>
            </el-form-item>
            <el-form-item label="消息日期" prop="msgDate">
                <el-date-picker v-model="queryForm.msgDate" placeholder="请选择消息日期" type="date" value-format="yyyy-MM-dd" size="small" clearable/>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" icon="el-icon-search" size="mini" @click="handleQuery">搜索</el-button>
                <el-button icon="el-icon-refresh" size="mini" @click="resetQuery()">重置</el-button>
            </el-form-item>
        </el-form>
        <!-- 查询按钮 -->
        <el-row :gutter="10" class="mb8">
            <el-col :span="1.5">
                <el-button type="primary" icon="el-icon-plus" size="mini" @click="openDialog({pageFrom:'add',title:'新增通讯信息'})" v-hasPermi="['userMsg:add']">新增</el-button>
            </el-col>
            <el-col :span="1.5">
                <el-button type="danger" icon="el-icon-delete" size="mini"  @click="handleDeleteAll" v-hasPermi="['userMsg:delete']">批量删除</el-button>
            </el-col>
            <right-toolbar @queryTable="getList"></right-toolbar>
        </el-row>
        <!-- 表格区域 -->
        <el-table border :header-cell-style="{background:'#e8f4ff'}" v-loading="loading" :data="dataList" @selection-change="handleSelectionChange">
            <el-table-column type="selection" align="center" width="50"/>
            <el-table-column label="id"  prop="id" v-if="false"/>
            <el-table-column label="序号" type="index" width="50"/>
            <el-table-column label="头像" prop="picture" sortable>
                <template slot-scope="scope">
                    <el-image :src="formatUrl(scope.row.picture)" class="column-image"></el-image>
                </template>
            </el-table-column>
            <el-table-column label="所属用户" prop="userId" sortable>
                <template slot-scope="scope">
                    <span v-for="wxUserRegister in wxUserRegisterDict" v-if="wxUserRegister.id==scope.row.userId">{{wxUserRegister.loginName}}</span>
                </template>
            </el-table-column>
            <el-table-column label="性别" prop="sex" sortable>
                <template slot-scope="scope">
                    <span v-for="dict in sexDict" v-if="dict.dictValue==scope.row.sex">{{dict.dictLabel}}</span>
                </template>
            </el-table-column>
            <el-table-column label="标题" prop="title" sortable/>
            <el-table-column label="消息内容" prop="content" show-overflow-tooltip sortable/>
            <el-table-column label="操作" align="center"  width="300" fixed="right">
                <template slot-scope="scope">
                    <el-button size="mini" type="info" plain icon="el-icon-view" @click="openDialog({title:'查看通讯信息',pageFrom:'view',id:scope.row.id})" v-hasPermi="['userMsg:view']">查看</el-button>
                    <el-button size="mini" type="primary" plain icon="el-icon-edit" @click="openDialog({title:'修改通讯信息',pageFrom:'edit',id:scope.row.id})" v-hasPermi="['userMsg:edit']">修改</el-button>
                    <el-button size="mini" type="danger" plain icon="el-icon-delete" @click="handleDelete(scope.row.id)"  v-hasPermi="['userMsg:delete']">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页区域 -->
        <div class="pagination">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :background="true"
                :current-page="queryForm.current"
                :page-sizes="[5,10,20,30,50]"
                :page-size="queryForm.pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import userMsgForm from './userMsgForm'
    export default {
        components: {userMsgForm},
        data() {
            return {
              loading: true,//是否加载完成
              selectedData: [],//勾选数据
              total: 0, //记录数
              wxUserRegisterDict:[],
              dataList: [],
              queryForm: {
                current: 1,
                size: 10,
                id: "",
                picture: "",
                userId: "",
                sex: "",
                title: "",
                msgDate: "",
                content: ""
            }
          };
        },
        mounted() {
            //初始化数据
            this.getList();
            //初始化【所属用户】
            this.getWxUserRegisterDict();
        },
        computed: {
            //【性别】
            sexDict() {
                return this.$store.getters.getDictArray('sys_sex');
            },
        },
        methods: {
            //点击搜索
            handleQuery() {
                this.queryForm.current = 1;
                this.getList();
            },
            //查询列表数据
            getList() {
                this.$request.doPost('/api/userMsg/getPage', this.queryForm).then(res => {
                    if (res.isOk) {
                        this.loading = false;
                        this.dataList = res.obj.records;
                        this.total = res.obj.total;
                    }
                })
            },
            //【所属用户】
            getWxUserRegisterDict(){
                this.$request.doPost('/api/wxUserRegister/getList', {}).then(res => {
                    if (res.isOk) {
                        this.wxUserRegisterDict=res.list;
                    }
                })
            },
            //重置查询
            resetQuery() {
                this.$refs['queryForm'].resetFields();
            },
            //打开弹窗
            openDialog(onLoad) {
                this.$refs.userMsgDialog.open(onLoad);
            },
            //选择页数
            handleSizeChange: function (size) {
                this.queryForm.size = size;
                this.getList();
            },
            //选择页码
            handleCurrentChange: function (currentPage) {
                this.queryForm.current = currentPage;
                this.getList();
            },
            //获取勾选数据
            handleSelectionChange(data) {
                this.selectedData = data;
            },
            //勾选批量删除
            handleDeleteAll() {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    if (that.selectedData.length > 0) {
                        that.selectedData.forEach(function (item, index) {
                            that.delete(item.id);
                        });
                        that.showSuccess("删除成功")
                    } else {
                        that.showWarn("至少选择一条数据！")
                    }
                });
            },
            //选中行删除
            handleDelete(id) {
                const that = this;
                that.showConfirm('确定删除选中的除数据吗?', function () {
                    that.delete(id);
                    that.showSuccess("删除成功");
                });
            },
            //删除方法
            delete(id) {
                this.$request.doGet('/api/userMsg/delete?id=' + id).then(res => {
                    if (res.isOk) {
                        this.getList();
                    };
                });
            },
        }
    };
</script>



