<!-- 【微信注册信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="userRegister" ref="userRegisterForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="头像" prop="headPicture">
        <sui-photo :url="userRegister.headPicture" v-model="userRegister.headPicture" :disabled="dialog.disabled"></sui-photo>
      </el-form-item>
      <el-form-item label="微信昵称" prop="wxUserInfoId">
        <el-select v-model="userRegister.wxUserInfoId" placeholder="请选择微信昵称" :disabled="dialog.disabled">
          <el-option v-for="wxUserInfo in wxUserInfoDict" :key="wxUserInfo.id" :value="wxUserInfo.id" :label="wxUserInfo.nickName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="角色" prop="roleType">
        <el-select v-model="userRegister.roleType" placeholder="选择角色" :disabled="dialog.disabled" >
          <el-option v-for="dict in roleTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="登录名" prop="loginName">
        <el-input v-model="userRegister.loginName" placeholder="请输入登录名" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="姓名" prop="realName">
        <el-input v-model="userRegister.realName" placeholder="请输入姓名" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
<!--      <el-form-item label="密码" prop="password">-->
<!--        <el-input v-model="userRegister.password" placeholder="请输入密码" :disabled="dialog.disabled" style="width: 217px"></el-input>-->
<!--      </el-form-item>-->
      <el-form-item label="邮箱" prop="email">
        <el-input v-model="userRegister.email" placeholder="请输入邮箱" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="手机号" prop="phone">
        <el-input v-model="userRegister.phone" placeholder="请输入手机号" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="身份证号" prop="idCard">
        <el-input v-model="userRegister.idCard" placeholder="请输入身份证号" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="详细地址" prop="address" >
        <el-input type="textarea" v-model="userRegister.address" placeholder="输入详细地址" :disabled="dialog.disabled"/>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('userRegisterForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  import suiPhoto from "@/components/sui-photo/sui-photo";
  export default {
    components: {
      suiPhoto,
    },
    name: "userRegisterForm",
    data() {
      return {
        wxUserInfoDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        userRegister: {
          headPicture: "",
          roleType: "",
          loginName: "",
          realName: "",
          password: "",
          email: "",
          phone: "",
          idCard: "",
          address: "",
          wxUserInfoId: ""
        },
        //表单校验
        rules: {
          headPicture: [{required: true, message: '请选择头像', trigger: 'change'}],
          roleType: [{required: true, message: '请选择角色', trigger: 'change'}],
          loginName: [{required: true, message: '请输入登录名', trigger: 'blur'}],
          realName: [{required: true, message: '请输入姓名', trigger: 'blur'}],
          // password: [{required: true, message: '请输入密码', trigger: 'blur'}],
          email: [{required: true, message: '请输入邮箱', trigger: 'blur'}],
          phone: [{required: true, message: '请输入手机号', trigger: 'blur'}],
          idCard: [{required: true, message: '请输入身份证号', trigger: 'blur'}],
          wxUserInfoId: [{required: true, message: '请选择微信昵称', trigger: 'change'}]
        }
      }
    },
    computed: {
      //【角色】
      roleTypeDict() {
        return this.$store.getters.getDictArray('role_type');
      },
    },
    methods: {
      //【基础表id】
      getWxUserInfoDict(){
        this.$request.doPost('/api/wxUserInfo/getList', {}).then(res => {
          if (res.isOk) {
            this.wxUserInfoDict=res.list;
          }
        })
      },
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getWxUserInfoDict();//加载【基础表id】
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getUserRegister(option.id)
        }
      },
      //根据id获取数据
      getUserRegister(id) {
        this.$request.doGet("/api/wxUserRegister/get?id="+id).then(res => {
          if (res.isOk) {
            this.userRegister = res.obj;
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/wxUserRegister/sub', that.userRegister).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.userRegisterForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>
</style>



