<!-- 【消息信息】弹窗页面 -->
<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.open">
    <el-form :model="sysMessage" ref="sysMessageForm" :rules="rules" label-width="100px" class="edit-form">
      <el-form-item label="关联数据" prop="pid">
        <el-input v-model="sysMessage.pid" placeholder="请输入关联数据" :disabled="dialog.disabled" style="width: 217px"></el-input>
      </el-form-item>
      <el-form-item label="关联用户" prop="userId">
        <el-select v-model="sysMessage.userId" placeholder="请选择关联用户" :disabled="dialog.disabled">
          <el-option v-for="sysUser in sysUserDict" :key="sysUser.id" :value="sysUser.id" :label="sysUser.loginName"/>
        </el-select>
      </el-form-item>
      <el-form-item label="消息类型" prop="msgType">
        <el-select v-model="sysMessage.msgType" placeholder="选择消息类型" :disabled="dialog.disabled" >
          <el-option v-for="dict in msgTypeDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
        </el-select>
      </el-form-item>
      <el-form-item label="消息内容" prop="content" >
        <el-input type="textarea" v-model="sysMessage.content" placeholder="输入消息内容" :disabled="dialog.disabled"/>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button size="small" @click="cancel">取 消</el-button>
      <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('sysMessageForm')">确 定</el-button>
    </div>
  </el-dialog>
</template>

<script>
  export default {
    components: {

    },
    name: "sysMessageForm",
    data() {
      return {
        sysUserDict:[],
        //弹窗设置
        dialog: {
          title: "",
          open: false,
          disabled: false
        },
        //表单数据
        sysMessage: {
          pid: "",
          userId: "",
          msgType: "",
          content: ""
        },
        //表单校验
        rules: {
          pid: [{required: true, message: '请输入关联数据', trigger: 'blur'}],
          userId: [{required: true, message: '请选择关联用户', trigger: 'change'}],
          msgType: [{required: true, message: '请选择消息类型', trigger: 'change'}],
        }
      }
    },
    computed: {
      //【消息类型】
      msgTypeDict() {
        return this.$store.getters.getDictArray('msg_type');
      },
    },
    methods: {
      //【关联用户】
      getSysUserDict(){
        this.$request.doPost('/api/sysUser/getList', {}).then(res => {
          if (res.isOk) {
            this.sysUserDict=res.list;
          }
        })
      },
      //打开弹窗
      open(option) {
        const pageFrom = option.pageFrom;
        const disabled = pageFrom == 'view' ? true : false;
        this.showDialog(option.title, disabled);
        this.getSysUserDict();//加载【关联用户】
        if (pageFrom == 'edit' || pageFrom == 'view') {
          this.getSysMessage(option.id)
        }
      },
      //根据id获取数据
      getSysMessage(id) {
        this.$request.doGet("/api/sysMessage/get?id="+id).then(res => {
          if (res.isOk) {
            this.sysMessage = res.obj;
          }
        });
      },
      //取消弹窗
      cancel: function () {
        this.dialog.open = false
      },
      //提交表单
      submitForm: function (formName) {
        const that = this;
        that.$refs[formName].validate((valid) => {
          if (valid) {//表达校验
            that.$request.doPost('/api/sysMessage/sub', that.sysMessage).then(res => {
              if (res.isOk) {
                that.dialog.open = false;
                that.showSuccess("提交成功");
                that.$emit('reloadList');
              }
            });
          }
        });
      },
      //显示弹窗
      showDialog(title, disabled) {
        this.resetForm(this.$refs.sysMessageForm);//重置表单
        this.dialog.open = true;//打开弹窗
        this.dialog.title = title;//设置标题
        this.dialog.disabled = disabled;//是否可编辑
      }
    }
  }
</script>

<style scoped>
</style>



