<!-- 【通讯信息】弹窗页面 -->
<template>
    <el-dialog :title="dialog.title" :visible.sync="dialog.open">
        <el-form :model="userMsg" ref="userMsgForm" :rules="rules" label-width="100px" class="edit-form">
            <el-form-item label="主键id" class="hidden" prop="id">
                <el-input type="hidden" v-model="userMsg.id"/>
            </el-form-item>
            <el-form-item label="头像" prop="picture">
                <sui-photo type="userMsg" v-model="userMsg.picture" :disabled="dialog.disabled"/>
            </el-form-item>
            <el-form-item label="所属用户" prop="userId">
                <el-select v-model="userMsg.userId" placeholder="请选择所属用户" :disabled="dialog.disabled">
                    <el-option v-for="wxUserRegister in wxUserRegisterDict" :key="wxUserRegister.id" :value="wxUserRegister.id" :label="wxUserRegister.loginName"/>
                </el-select>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
                <el-select v-model="userMsg.sex" placeholder="选择性别" :disabled="dialog.disabled" >
                    <el-option v-for="dict in sexDict" :key="dict.dictValue" :label="dict.dictLabel" :value="dict.dictValue"/>
                </el-select>
            </el-form-item>
            <el-form-item label="标题" prop="title">
                <el-input v-model="userMsg.title" placeholder="请输入标题" :disabled="dialog.disabled" class="sui-input"/>
            </el-form-item>
            <el-form-item label="消息日期" prop="msgDate">
                <el-date-picker v-model="userMsg.msgDate" placeholder="选择消息日期" :disabled="dialog.disabled" type="date" value-format="yyyy-MM-dd"/>
            </el-form-item>
            <el-form-item label="消息内容" prop="content">
                <el-input type="textarea" v-model="userMsg.content" placeholder="输入消息内容" :disabled="dialog.disabled"/>
            </el-form-item>
            <el-form-item label="附件列表" prop="fileList">
                <sui-file type="userMsg" :refId="userMsg.id" v-model="userMsg.fileList" :disabled="dialog.disabled"/>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="small" @click="cancel">取 消</el-button>
            <el-button size="small" v-if="!dialog.disabled" type="primary" @click="submitForm('userMsgForm')">确 定</el-button>
        </div>
    </el-dialog>
</template>

<script>
import suiPhoto from "@/components/sui-photo/sui-photo";
import suiFile from "@/components/sui-file/sui-file";
export default {
    components: {
        suiFile,suiPhoto,
    },
    name: "userMsgForm",
    data() {
      return {
        wxUserRegisterDict:[],
        //弹窗设置
        dialog: {
            title: "",
            open: false,
            disabled: false
        },
        //表单数据
        userMsg: {
            id: "",
            picture: "",
            userId: "",
            sex: "",
            title: "",
            msgDate: "",
            content: ""
        },
        //表单校验
        rules: {
            picture: [{required: true, message: '请选择头像', trigger: 'change'}],
            userId: [{required: true, message: '请选择所属用户', trigger: 'change'}],
            sex: [{required: true, message: '请选择性别', trigger: 'change'}],
            title: [{required: true, message: '请输入标题', trigger: 'blur'}],
            msgDate: [{required: true, message: '请选择消息日期', trigger: 'change'}],
            content: [{required: true, message: '请输入消息内容', trigger: 'blur'}]
        }
      }
    },
    computed: {
        //【性别】
        sexDict() {
            return this.$store.getters.getDictArray('sys_sex');
        },
    },
    methods: {
        //【所属用户】
        getWxUserRegisterDict(){
            this.$request.doPost('/api/wxUserRegister/getList', {}).then(res => {
                if (res.isOk) {
                    this.wxUserRegisterDict=res.list;
                }
            })
        },
        //打开弹窗
        open(option) {
            const pageFrom = option.pageFrom;
            const disabled = pageFrom == 'view' ? true : false;
            this.showDialog(option.title, disabled);
            this.getWxUserRegisterDict();//加载【所属用户】
            if (pageFrom == 'edit' || pageFrom == 'view') {
                this.getUserMsg(option.id)
            }
        },
        //根据id获取数据
        getUserMsg(id) {
            this.$request.doGet("/api/userMsg/get?id="+id).then(res => {
                if (res.isOk) {
                    this.userMsg = res.obj;
                }
            });
        },
        //取消弹窗
        cancel: function () {
            this.dialog.open = false
        },
        //提交表单
        submitForm: function (formName) {
            const that = this;
            that.$refs[formName].validate((valid) => {
                if (valid) {//表达校验
                    that.$request.doPost('/api/userMsg/sub', that.userMsg).then(res => {
                        if (res.isOk) {
                            that.dialog.open = false;
                            that.showSuccess("提交成功");
                            that.$emit('reloadList');
                        }
                    });
                }
            });
        },
        //显示弹窗
        showDialog(title, disabled) {
            this.resetForm(this.$refs.userMsgForm);//重置表单
            this.dialog.open = true;//打开弹窗
            this.dialog.title = title;//设置标题
            this.dialog.disabled = disabled;//是否可编辑
        }
    }
}
</script>

<style scoped>
</style>



